.form-page {
    background-image: url(/images/pexels-vincent-gerbouin-1174732.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 150px 0 50px 0;
}

.form-page section{
    
    padding: 35px 40px;
    background-image: url(/images/formShap.svg);
    background-color: rgb(0, 0, 0,0.1);
    background-repeat: no-repeat;
    border-radius: 15px;
}

.form-page section .logo {
    position: absolute;
    top: 55%;
    left: 61%;
    transform: translate(-50% , -50%);
}

.form-page section form .btn-submit {
    background-image: linear-gradient(to right,#47C1F0,#3235E8) ;
    padding: 8px 35px;
    margin-top: 15px;
    border-radius: 35px;
    border: none;
    color: white;
    font-weight: 600;
}

.form-page section h2 {
    font-size: 50px;
    font-weight: bold;
}

.form-page form input {
    border-radius: 10px;
}

.password-group {
    position: relative;
}

.password-group i {
    position: absolute;
    right: 0;
    top: 43px;    
    margin-right: 20px;  
    cursor: pointer;
    color: #aaaeb3;
}

@media (max-width: 990px) {
    .form-page {
        height: 989px;
    } 
    .form-page section {
        margin-top: 0px;
    }
    .form-page section .logo {
        display: none;
    }
    .form-page form {
        padding-top: 165px;
    }
    .form-page section h4 {
        margin-top: 25px;
    }
    .form-page section form .btn-submit {
        display: flex;
        margin-left: auto;
        margin-right: auto;
    }
}