* {
  
}

body {
  font-family: 'Poppins', sans-serif;
}

html {
  scroll-behavior: smooth;
  overflow: hidden;
}



/* Sart of on-lode section */

.on-load {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #c3eaf9;
  z-index: 9999999;
}

.on-load img {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50% , -50%) rotate(0deg);
  animation: 2s spinner infinite ease-in-out;
}


@keyframes spinner {
  0% {
      transform: translate(-50% , -50%) rotate(360deg);
  }
}
/* End of on-lode section */


/* Sart of Navebar */

nav {
  background-color: rgb(255, 255, 255 , 0.7);
  box-shadow: 0 0 5px 0px black;
}

.nav-item .active {
  color: var(--primary1) !important;
  transition: all 0.6s;

}

.nav-item a {
  position: relative;
  transition: all 0.6s;
}

.nav-item a.active::after {
  content: '';
  position: absolute;
  bottom: -20px;
  left: 5px;
  width: 90%;
  height: 2px;
  background-color: var(--primary1);
}

.btn-submit {
    /* background-image: linear-gradient(to right,#47C1F0,#3235E8) ; */
    background-color: var(--primary1);
    padding: 8px 35px;
    border-radius: 35px;
    border: none;
    color: white;
    font-weight: 600;
}

@media (max-width: 990px) {
  .nav-item {
      margin-left: 15px;
  }
  .nav-item a.active::after {
      bottom: 0;
      left: 0;
  }
}

/* End of Navebar */

/* Start of Header */

header {
  position: relative;
  background-image: url('images/pexels-vincent-gerbouin-1174732.png');  
  background-position: center;
  background-size: cover;
  height: 100vh;
}

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50% , -50%);
}

.center p {
  width: 400px;
  margin-right: auto ;
  margin-left: auto ; 
}


.social {
  position: fixed;
  top: 50%;
  right: 65px; 
  transform: translate(0,-50%);
  z-index: 10;
}

.social ul {
  list-style: none; 
}


.social a {
  position: relative;
  height: 40px;
  width: 40px;
  
  border-radius: 50%;
  color: white;
  text-decoration: none;
  transition: all 0.4s;
}

.social a::before {
  position: absolute;
  z-index: 1;
  left: 50%;
  top: 50%;
  
  transform: translate(-50%,-50%);
}

.social a::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: solid 2px white;
  z-index: 0;
  transition: all 0.4s;
}

.social a:hover {
  color: var(--primary1);
}

.social a:hover::after {
  background-color: white;
}

@media (max-width: 990px) {
  header {
      height: 70vh;
  }

  h1 {
      font-size: 40px;
  }

  .social {
      position: absolute;
      right: 15px;
      top: 50%;
  }

  .center p {
      width: 250px;
  }
}

/* End of Header */

/* Start of About section */

.about {
  padding: 100px 50px;
}

.about .video-col {
  position: relative;
  height: 460px;
  background-image: url(./images/image.png);
  background-position: center;
  background-size: cover;
}

.about .video-col .video-content {
  position: absolute;
  bottom: 50px;
  left: 50px;
  color: white;

}

.about .video-col .video-content .play-icon {
  width: 66px;
  height: 66px;
  line-height: 66px;
  border-radius: 50%;
  font-size: 25px;
  text-align: center;
  background-color: var(--primary1);
  cursor: pointer;
}

.about .travisit-word {
  top: -30px;
  left: 10px;
  
}

.about .about-text h4 {
  font-size: 25px;
  padding: 20px 0;
  color: var(--primary1);
}

.about .about-text h2 {
  font-size: 28px; 
}

.about .about-text p {
  font-size: 14px; 
  margin: 35px 0;
}

.about .video-frame {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgb(0, 0, 0 , 0.7);
  z-index: 8888;
}

.about .video-frame iframe {
  width: 960px;
  height: 615px;
}

.about .video-frame.hidden {
  visibility: hidden; 
}

@media (max-width: 990px) {
  .about .video-col {
      height: 300px;
      margin-bottom: 20px;
  }

  .about .video-col .video-content {
      bottom: 20px;
      left: 20px;
  }

  .about .video-frame iframe {
      width: 460px;
      height: 315px;
  }
}

/* End of About section */


