/* Start of Footer */

footer {
  background-color: #232d54;
  color: white;
  padding-top: 60px;
}

footer h5 {
  position: relative;
  padding-bottom: 15px;
  margin-bottom: 25px;
}

footer h5::before {
  content: ''; 
  position: absolute;
  left: 0;
  bottom: 0;
  
  width: 70%;
  height: 1px;
  background-color: white;

}

footer h5::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  
  width: 20%;
  height: 2px;
  background-color: var(--primary1);

}

footer ul {
  position: absolute;
  left: 10px; 
  list-style: none;
}

footer .quick-link i { 
  color: var(--primary1);
}

footer .quick-link li {
  margin-bottom: 5px;
}

footer .get-app li {
  font-size: 16px;
  padding: 13px 27px;
  margin-bottom: 10px;
  border: solid 1px var(--primary1);
  transition: all 0.2s ;
}

footer .get-app li a {
  text-decoration: none;
  color: white;
}

footer .get-app li:hover {
  background-color: var(--primary1); 
}

footer .go-top {
  border: solid 1px var(--primary1);
}

footer .go-top a {
  text-decoration: none;
  color: white;
}

.copy-rights {
  background-color: #1e2951;
}

@media (max-width: 990px) {
  footer ul {
      position: relative;
      left: -20px;
  }
}

/* End of Footer */

